*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  font-family: sans-serif;
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -ms-overflow-style: scrollbar;
  -webkit-tap-highlight-color: transparent;
}

@-ms-viewport {
  width: device-width;
}

article, aside, dialog, figcaption, figure, footer, header, hgroup, main, nav, section {
  display: block;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #212529;
  text-align: left;
  background-color: #fff;
}

[tabindex="-1"]:focus {
  outline: none !important;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
  margin-bottom: 0.5rem;
}

p {
  margin-top: 0;
  margin-bottom: 1rem;
}

abbr[title],
abbr[data-original-title] {
  text-decoration: underline;
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
  cursor: help;
  border-bottom: 0;
}

address {
  margin-bottom: 1rem;
  font-style: normal;
  line-height: inherit;
}

ol,
ul,
dl {
  margin-top: 0;
  margin-bottom: 1rem;
}

ol ol,
ul ul,
ol ul,
ul ol {
  margin-bottom: 0;
}

dt {
  font-weight: 700;
}

dd {
  margin-bottom: .5rem;
  margin-left: 0;
}

blockquote {
  margin: 0 0 1rem;
}

dfn {
  font-style: italic;
}

b,
strong {
  font-weight: bold;
}

small {
  font-size: 80%;
}

sub,
sup {
  position: relative;
  font-size: 75%;
  line-height: 0;
  vertical-align: baseline;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

a {
  color: #007bff;
  text-decoration: none;
  background-color: transparent;
  -webkit-text-decoration-skip: objects;
}

a:hover {
  color: #0056b3;
  text-decoration: underline;
}

a:not([href]):not([tabindex]) {
  color: inherit;
  text-decoration: none;
}

a:not([href]):not([tabindex]):focus, a:not([href]):not([tabindex]):hover {
  color: inherit;
  text-decoration: none;
}

a:not([href]):not([tabindex]):focus {
  outline: 0;
}

pre,
code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

pre {
  margin-top: 0;
  margin-bottom: 1rem;
  overflow: auto;
  -ms-overflow-style: scrollbar;
}

figure {
  margin: 0 0 1rem;
}

img {
  vertical-align: middle;
  border-style: none;
}

svg:not(:root) {
  overflow: hidden;
}

a,
area,
button,
[role="button"],
input:not([type="range"]),
label,
select,
summary,
textarea {
  -ms-touch-action: manipulation;
  touch-action: manipulation;
}

table {
  border-collapse: collapse;
}

caption {
  padding-top: 0.75rem;
  padding-bottom: 0.75rem;
  color: #868e96;
  text-align: left;
  caption-side: bottom;
}

th {
  text-align: inherit;
}

label {
  display: inline-block;
  margin-bottom: .5rem;
}

button {
  border-radius: 0;
}

button:focus {
  outline: 1px dotted;
  outline: 5px auto -webkit-focus-ring-color;
}

input,
button,
select,
optgroup,
textarea {
  margin: 0;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
html [type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  padding: 0;
  border-style: none;
}

input[type="radio"],
input[type="checkbox"] {
  box-sizing: border-box;
  padding: 0;
}

input[type="date"],
input[type="time"],
input[type="datetime-local"],
input[type="month"] {
  -webkit-appearance: listbox;
}

textarea {
  overflow: auto;
  resize: vertical;
}

fieldset {
  min-width: 0;
  padding: 0;
  margin: 0;
  border: 0;
}

legend {
  display: block;
  width: 100%;
  max-width: 100%;
  padding: 0;
  margin-bottom: .5rem;
  font-size: 1.5rem;
  line-height: inherit;
  color: inherit;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  outline-offset: -2px;
  -webkit-appearance: none;
}

[type="search"]::-webkit-search-cancel-button,
[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  font: inherit;
  -webkit-appearance: button;
}

output {
  display: inline-block;
}

summary {
  display: list-item;
}

template {
  display: none;
}

[hidden] {
  display: none !important;
}

.barrnav ul {
  display: flex;
  flex-direction: column;
  background-color: #B4D143;
}

.barrnav img.hamb {
  width: 50px;
  height: 50px;
}

.barrnav li {
  list-style-type: none;
}

.barrnav li:hover {
  background-color: #768c22;
}

@media screen and (min-width: 768px) {
  .barrnav ul {
    flex-direction: row;
    justify-content: space-around;
  }
}

.inner {
  width: 100%;
}

.backgroud_homme {
  width: 250%;
  margin: 0 -75%;
}

.backgroud {
  margin-top: -80px;
}

.figcap_homme {
  color: white;
  position: relative;
  top: 30em;
  background-color: rgba(255, 255, 255, 0.2);
}

.promo {
  color: white;
  background-color: red;
  position: relative;
  top: 20em;
  border-radius: 10px;
  width: 50%;
  margin: auto;
}

@media screen and (min-width: 466px) {
  .inner {
    width: 466px;
    margin: 0 auto;
  }
}

@media screen and (min-width: 668px) {
  .inner {
    width: 668px;
  }
}

.logo_and_rdv {
  display: flex;
  flex-direction: row;
  margin-bottom: 1rem;
}

.logo_texte, .button_rdv {
  width: 100%;
}

.div_logo_texte, .div_lien_rdv {
  width: 50%;
}

.img_experience {
  width: 100%;
}

.experience {
  width: 60%;
  margin: 0 auto;
}

.cryolyse {
  order: 2;
}

@media screen and (min-width: 466px) {
  .experiences {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    margin-bottom: 1rem;
  }
  .backgroud_homme {
    width: 180%;
    margin: 0 -50%;
  }
  .experience {
    width: 40%;
    margin: 0 auto;
  }
  .cryolyse {
    order: 0;
  }
}

@media screen and (min-width: 668px) {
  .backgroud_homme {
    width: 150%;
    margin: 0 -50%;
  }
  .figcap_homme {
    top: 35em;
  }
  .experience {
    width: 30%;
    margin: 0 auto;
  }
}

@font-face {
  font-family: 'Bernina';
  src: url("fonts/BerninaSans-Web-CondensedBold.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
}

body {
  color: #00465b;
  text-align: center;
  background-color: azure;
}

.titre_ice, .experiences {
  font-family: "Bernina";
}

.button_rdv {
  border: solid #00465b 2px;
  border-radius: 10px;
  background-color: azure;
  padding: 10px;
  font-size: 150%;
}

.img_experience {
  border-radius: 100%;
}

.libelle_cryo {
  margin: 4% 0;
}

.titre_ice {
  text-align: start;
}

@media screen and (min-width: 668px) {
  .titre_ice {
    text-align: end;
  }
}
